body{
    margin: 0;
    padding: 0;
}
#root{
    margin: 0;
    display: flex;
    flex-direction: column;
    padding: 0;

    
}
.head{
    width: 100%;
}
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
